.noteWrapper{
    display: flex;

    align-items: center;
    justify-content: center;
}

.noteContent{
    flex: 1;
    margin: 2em;

    max-width: 80%;

    font-family: "Source Sans Pro", sans-serif;
    color: #655057;
}

.noteContent h3{
    margin-top: 1em;
    margin-bottom: 1em;
    text-align: center;

    font-size: 2em;
}

.noteContent h5{
    margin-top: 1em;
    margin-bottom: 1em;

    font-size: 1.4em;
}

.noteContent p{
    margin-top: 0.6em;
    margin-bottom: 0.6em;
}

.noteContent ul{
    list-style: none;
}

.noteContent li{
    margin-bottom: 2em;
}

.noteBack{
    position: absolute;

    left: 4em;
    top: 2em;
}

.noteBack svg{
    height: 2em;
    width: 2em;

    color: #655057;
}

.noteBack:hover svg{
    transform: scale(1.1);
}