/* Global */
*{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

a{
    text-decoration: none;
}

body{
    overflow-x: hidden;
}

#mainContent{
    height: 100vh;
    overflow-y: scroll;
}

/* Bg */
#bg{
    position: fixed;
    height: 100vh;
    width: 100vw;
    background-image: linear-gradient(to top right, #cac3cb, #d5c8cf);

    z-index: -1;
}

#bgDecoration{
    position: absolute;
    bottom: 0;
    left: 5em;

    height: 20em;
}

/* Animation */
@keyframes blink {
    20%{
        opacity: 1;
    }
    50%{
        opacity: 0;
    }
    80%{
        opacity: 1;
    }
}

@keyframes blink2 {
    0%{
        opacity: 0;
    }
    60%{
        opacity: 1;
    }
}


@keyframes blink3 {
    20%{
        opacity: 0;
    }
    80%{
        opacity: 1;
    }
}

@keyframes fade {
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}

@keyframes show {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

/* Cover */
#cover{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    position: relative;

    height: 100vh;
    width: 100vw;

    font-family: 'Press Start 2P', cursive !important;
    color: #655057;

    animation: show 0.8s ease-in-out;
}

#cover.fading{
    opacity: 0;
    animation: fade 0.8s ease-in-out;
}

#coverIcon{
    position: absolute;
    right: 5em;
    bottom: 0;

    height: 10em;
}

#coverTitle{
    margin: 3em;

    font-size: 3em;
}

#discover{
    width: 100%;

    margin: 1em 1em 2em;
    font-size: 1.2em;
    text-align: center;

    z-index: 2;
    cursor: pointer;
}

#discover span{
    margin: 0.32em;
}

#discoverEnd{
    animation: blink 1s infinite ease-in-out;
}

#cover svg{
    height: 1.6em;
    margin: 0.1em;
}

.clickable{
    cursor: pointer;
}

#coverArrow1{
    animation: blink2 1.6s infinite ease-in-out;
}

#coverArrow2{
    animation: blink3 1.6s infinite ease-in-out;
}

/* Main content */
#mainContent{
    position: relative;

    animation: show 0.8s ease-in-out;
}

.hidden{
    display: none;
}

/* Nav */
#nav{
    display: flex;

    position: sticky;
    top: 0;
    height: 2.4em;

    font-family: 'Ubuntu Mono', monospace;
    font-size: 2em;
    background-color: #655057;

    z-index: 3;
}

#nav ul{
    display: flex;
    flex-direction: row;
    width: 20em;

    margin-left: 1.8em;

    list-style: none;
}

#nav .focus{
    font-weight: bold;
    border-bottom: 8px solid rgba(0, 0, 0, 0.2);
}

#nav a{
    display: flex;
    justify-content: center;
    flex: 1;
    box-sizing: border-box;

    padding: 0.4em;
    color: #C9BCC5;
}

#nav a:hover{
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.4);
}

#nav li{
    margin-top: 4px;
}

#navIcon{
    position: absolute;
    right: 1.6em;
    bottom: 0;

    height: 2.4em;
}

/* pages */
.page{
    display: flex;
    flex-direction: column;
    align-items: center;

    min-height: 100vh;
}

.page .space{
    height: 4.8em;
}

.page .title{
    margin-top: 1em;
    margin-bottom: 1em;

    font-family: 'Luckiest Guy', cursive;
    font-size: 3.2em;

    color: #655057;
}

/* about */
#aboutContent{
    display: flex;
    flex-direction: row;

    margin-left: 10em;
    margin-right: 10em;
}

#aboutContent div{
    display: flex;
    align-items: center;
    justify-content: center;
}

#aboutContent img{
    height: 10em;
    width: 10em;

    border-radius: 50%;
    border: 1px solid #655057;
}

#aboutContent p{
    margin-left: 4em;
    font-size: 1.6em;
}

/* works */
#workCards{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    width: 42em;

    margin-top: -1em;
}

.workCard{
    position: relative;

    display: inline-flex;
    width: 10em;
    margin: 1em 2em;

    align-items: center;
    justify-content: center;
}

.workIcon{
    display: block;
    width: 8em;
    height: 8em;
}

.workCard div{
    display: none;

    position: absolute;
    width: 8em;
    height: 8em;
}

.workCard:hover{
    cursor: pointer;
}

.workCard:hover div{
    display: block;
    background-color: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(2px);
    -webkit-backdrop-filter: blur(2px);
}

.workCard svg{
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -0.7em;
    margin-top: -0.7em;

    height: 1.4em;
    width: 1.4em;

    color: #655057;
}

.detailedWorkCard{
    display: flex;
    align-items: center;
    justify-content: center;

    position:fixed;

    top:0;
    left:0;

    width: 100%;
    height: 100%;
    background:rgba(0, 0, 0, 0.5);
    z-index: 3;
}

.workCardInner{
    display: flex;
    flex-direction: column;

    width: 30em;

    background-color: white;

    animation: show 0.4s ease-in-out;
}

.preview{
    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;
    height: 12em;
    width: 100%;

    margin-bottom: 1em;

    background-color: rgba(0, 0, 0, 0.04);
}

.previewImg{
    height: 100%;
    width: auto;
}

.preview .dots{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    width: 100%;

    position: absolute;
    bottom: 0;
}

.preview .dot{
    display: block;
    height: 1em;
    width: 1em;

    border: 1px solid #655057;
    border-radius: 50%;

    margin: 1em 1em 0.4em;

    opacity: 0.8;
    background-color: aliceblue;
}

.preview .dot.focus, .preview .dot:hover{
    border-color: aliceblue;
    background-color: #655057;
    cursor: pointer;
}

.workName{
    margin-left: 2em;

    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.6em;
    color: #655057;
}

.workDescription{
    margin-left: 3.2em;
    margin-right: 3.2em;
    margin-bottom: 0.4em;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1em;
    color: #655057;
}

.workDemos{
    display: flex;

    margin-left: 2.67em;
    margin-bottom: 0.4em;

    color: #655057;
    font-size: 1.2em;
}

.workDemos div, .workDemos a{
    display: inline-flex;
    align-items: center;
}

.workDemos a:hover{
    opacity: 0.6;

    cursor: pointer;
}

.workDemos svg{
    height: 1em;
    width: 1em;

    margin-left: 1em;
}

.workBottom{
    display: flex;

    background-color: #655057;
}

.workBottom div, .workBottom a{
    display: block;

    padding: 0.8em;
    flex: 1;

    color: aliceblue;
    font-family: "Source Sans Pro", sans-serif;
    font-size: 1.2em;
    text-align: center;
}


.workBottom div:hover, .workBottom a:hover{
    cursor: pointer;
    background-color: rgba(0,0,0,0.4);
}

/* Notes */
#noteArea{
    display: flex;
    flex-direction: column;
    width: 60%;
}

.note{
    position: relative;

    padding: 1.2em 2em;

    border-radius: 4px;
    margin-bottom: 2em;

    color: #655057;
    background-color: #C9BCC5;
}

.note:hover{
    background-color: rgba(255, 255, 255, 0.4);
    cursor: pointer;
}

.noteName{
    font-size: 1.4em;

    font-family: 'Source Sans Pro', sans-serif;
}

.noteTag{
    position: absolute;
    right: 2em;
    bottom: 0.9em;

    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.1em;
}

.noteTag svg{
    height: 1em;
    width: 1em;

    margin-right: 0.32em;
    margin-bottom: -4px;
}

.noteDate{
    margin-top: 1em;

    font-family: 'Source Sans Pro', sans-serif;
    font-size: 0.96em;
}

/* Contact */
.contactDetail{
    display: flex;
    flex-direction: row;

    list-style: none;

    width: 36em;
    margin-bottom: 3em;
}

.contactDetail a{
    display: block;
    position: relative;

    flex: 1;

    padding: 0.8em;
    margin-left: 1em;
    margin-right: 1em;

    text-align: center;
    font-size: 1.6em;
    color: #655057;
}

.contactDetail .toolTip{
    display: none;
    position: absolute;

    width: 100%;

    left: -0.2em;
    bottom: -2em;

    padding: 0.4em;
    border: 1px solid #655057;

    font-size: 0.6em;
}

.contactDetail a:hover .toolTip{
    display: block;
}

.contactDetail svg{
    height: 1.6em;
    width: 1.6em;
    color: #655057;
}

.contactDetail a:hover svg{
    opacity: 0.8;
    transform: scale(1.1);
}

.leaflet-container {
    width: 100vw;
    height: 40vh;
}

@media only screen and (max-width: 1200px){
    #bgDecoration{
        left: 3em;
    }

    #nav{
        font-size: 1.9em;
    }

    #nav ul{
        margin-left: 1.2em;
    }

    .page .space{
        height: 3em;
    }

    .page .title{
        font-size: 3em;

        margin-top: 0.8em;
        margin-bottom: 0.8em;
    }

    #aboutContent{
        margin-left: 6em;
        margin-right: 6em;
    }

    #noteArea {
        width: 70%;
    }
}

@media only screen and (max-width: 992px) {
    body{
        font-size: 0.9em;
    }

    #bgDecoration{
        left: 2em;
    }

    #coverTitle{
        margin: 2em;
    }

    #coverIcon{
        right: 3em;
    }

    #discover span{
        margin: 0.2em;
    }

    #nav ul{
        width: 16em;
        margin-left: 1em;
    }

    #navIcon{
        right: 1em;
    }

    .page .title{
        font-size: 2.8em;
    }

    #aboutContent{
        margin-left: 4em;
        margin-right: 4em;
    }

    #noteArea {
        width: 80%;
    }
}

/* for mobile devices */
@media only screen and (max-width: 768px) {
    body{
        font-size: 0.8em;
    }

    #bgDecoration{
        height: 14em;
        left: 1em;
    }

    #coverTitle{
        margin: 2em;
        font-size: 2.4em;
    }

    #coverIcon{
        height: 8em;
        right: 1em;
    }

    #discover span{
        margin: 0.1em;
    }

    #nav{
        font-size: 1em;
    }

    #nav ul{
        margin-left: 0.6em;
    }

    #navIcon{
        right: 0.6em;
    }

    #aboutContent{
        margin-left: 1em;
        margin-right: 1em;
    }

    #aboutContent p{
        font-size: 1.4em;
        margin-left: 1em;
    }

    #aboutContent img{
        height: 8em;
        width: 8em;
    }

    #workCards{
        width: 100%;
    }

    .workCard{
        width: 49%;
        margin-left: 0;
        margin-right:  0;
    }

    .preview{
        font-size: 0.98em;
    }

    #contact{
        min-height: 0;
    }

    .contactDetail{
        width: 100vw;
        text-align: center;
    }

    .contactDetail a{
        margin: 0;
    }
}